/*BORDER RADIUS*/
.desktop__video {
  border-radius: 4px;
  border: 1px solid rgba(15, 27, 94, 0.08);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  gap: 0;
}
.desktop__video video {
  max-width: 680px;
}

.research__list {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.problems__items {
  display: flex;
  flex-direction: column;
  gap: 80px;
}
.problems__item {
  display: flex;
  flex-direction: column;
  gap: 24px;
}
.problems__content {
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.problems__title {
  font-weight: 500;
}
.problems__text {
  color: #888;
}
.problems img {
  max-width: 680px;
  width: 100%;
  border-radius: 4px;
}

/*MOBILE*/
.mobile {
  background-color: #272727;
  text-align: center;
}
.mobile__video video {
  border-radius: 4px;
}

.logo__content {
  display: flex;
  flex-direction: column;
  gap: 32px;
}
.logo__img {
  display: flex;
  flex-direction: column;
  gap: 13px;
}
.logo__img--main img {
  max-width: 680px;
  width: 100%;
}
.logo__img--small {
  display: flex;
  gap: 13px;
}
.logo__img--small img {
  max-width: 333px;
  width: 100%;
}
.logo__text {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.navigation__list {
  display: flex;
  flex-direction: column;
  gap: 24px;
}